.product_box {
  display: flex;
  justify-content: space-between;
  width: 80%;

  margin: 0 auto 20px;
  padding: 15px;

  text-align: center;

  border-radius: 12px;
  transition: all 0.3s;
  background-color: #1e2c3a;
}

.product_box__true {
  display: flex;
  justify-content: space-between;
  width: 60%;

  margin: 0 auto 20px;
  padding: 10px 15px;

  font-size: 28px;
  text-align: center;

  border-radius: 12px;
  transition: all 0.3s;

  background-color: #1e2c3a;
  box-shadow: inset 0 0 15px greenyellow;
}

.product_left {
  display: flex;
  width: 85%;
}

.title {
  display: block;
  width: 85%;
  font-size: 22px;
  word-wrap: break-word;
  text-align: left;
  line-height: 34px;
}

.title_true {
  display: block;
  width: 74%;
  word-wrap: break-word;
  text-align: left;
  font-size: 22px;
  line-height: 34px;
  text-decoration: line-through;
  color: greenyellow;
}

.trash {
  display: block;
  min-width: 34px;
  height: 34px;
  margin-left: 25px;
  border: 0;
  border-radius: 10px;
  border: 1px solid #cc2525;
  background-color: #1e2c3a;
  cursor: pointer;
}

.trash:hover {
  box-shadow: 0 0 15px #cc2525;
}

.trash_icon {
  display: block;
  width: 30px;
  height: 30px;
  margin: auto;
  border-radius: 10px;
  color: #cc2525;
}

@media (max-width: 666px) {
  .product_box {
    width: 95%;
    margin-bottom: 15px;
  }
  .product_left {
    width: 70%;
  }
  .title {
    font-size: 20px;
  }
  .title_true {
    font-size: 20px;
  }
  .product_box__true {
    width: 70%;
    margin-bottom: 15px;
  }
}

@media (max-width: 360px) {
  .product_left {
    width: 60%;
  }
  .title {
    font-size: 18px;
  }
  .title_true {
    font-size: 18px;
  }
  .product_box__true {
    width: 85%;
  }
}
