.icon_box {
    width: 34px;
    height: 34px;
    margin-right: 25px;
    border-radius: 10px;
    border: 1px solid yellow;
    cursor: pointer;
}

.icon_box:hover {
    box-shadow: 0 0 5px yellow;
}

.icon {
    display: block;
    width: 33px;
    height: 33px;
    margin: auto;
    border-radius: 10px;
    box-shadow: 0 0 15px greenyellow;
    color: greenyellow;
}

@media (max-width: 666px) {
    .icon_box {
        margin-right: 15px;
    }
}

@media (max-width: 360px) {
    .icon_box {
        margin-right: 10px;
    }
}
